@import url(https://fonts.google.com/specimen/Oswald?query=osw);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  width: 320px;
  margin-left: 42.5%;
  margin-top: 10px;
  text-align: center;
  font-family: 'Oswald';
}
.background{
  width: 320px;
  height: 200px;
  background-image: url(data:image/jpeg;base64,);
  /*
  background-image: url("http://picsum.photos/320/200?random=1&blur=2");
  */
}
.title{
  margin-left: 5%;
  text-align: left;
}
.logo{
  width: 64px;
  margin-top: 68px;
  border: white 3px solid;
  border-radius: 50px;

}
@media (max-width: 980px) {
  .App{
    margin-left: 38%;
  }
}
@media (max-width: 699px) {
  .App{
    margin-left: 28%;
  }
}
@media (max-width: 480px) {
  .App{
    margin-left: 5%;
  }
}
@media (max-width: 320px) {
  .App{
    margin-top: 0;
    margin-left: 0;
  }
}

.add-input{
    width: 90%;
    margin-left: 5%;
}
.add-input input{
    width: 98%;
    height: 48px;
    box-shadow: 0 0 4px 4px lightgrey inset;
    border: lightgrey 1px solid;
    border-radius: 10px;
}
.add-task{
    width: 90%;
    margin-top: 16px;
    background-color: #007d19;
    border-radius: 10px;
    color: white;
}
.add-task h1{
    padding: 10px;
}
.task{
    text-align: left;
    margin-left: 5%;
    margin-bottom: 16px;
    color: red;
    font-weight: bold;
}
.done{
    color: #007d19;
    text-decoration: line-through;
}
